'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';

export default function AdminPage() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  
  // Data state
  const [activeTab, setActiveTab] = useState<'appointments' | 'volunteers' | 'contacts' | 'banners' | 'services' | 'aboutImage'>('appointments');
  const [data, setData] = useState<any[]>([]);
  const [loading, setLoading] = useState(false);
  
  // Form states
  const [newItem, setNewItem] = useState<any>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  // In a real app, this should be done via secure session/cookie
  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault();
    if (password === 'admin123') { // Placeholder secure password
      setIsAuthenticated(true);
      setError('');
    } else {
      setError('Invalid password');
    }
  };

  useEffect(() => {
    if (isAuthenticated) {
      fetchData(activeTab);
    }
  }, [isAuthenticated, activeTab]);

  const fetchData = async (type: string) => {
    setLoading(true);
    try {
      let result = [];
      if (type === 'banners') {
         const { getBanners } = await import('../actions/banner');
         result = await getBanners();
      } else if (type === 'services') {
         const { getServices } = await import('../actions/service');
         result = await getServices();
      } else if (type === 'aboutImage') {
         const { getAllAboutImages } = await import('../actions/aboutImage');
         result = await getAllAboutImages();
      } else {
         const { getAdminData } = await import('../actions/admin');
         // @ts-ignore
         result = await getAdminData(type);
      }
      setData(result);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (id: string, type: string) => {
    if (!confirm('Are you sure you want to delete this item?')) return;
    try {
      if (type === 'banners') {
        const { deleteBanner } = await import('../actions/banner');
        await deleteBanner(id);
      } else if (type === 'services') {
        const { deleteService } = await import('../actions/service');
        await deleteService(id);
      } else if (type === 'aboutImage') {
        const { deleteAboutImage } = await import('../actions/aboutImage');
        await deleteAboutImage(id);
      }
      fetchData(type);
    } catch (error) {
      alert('Failed to delete item');
    }
  };

  const handleCreate = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    try {
      if (activeTab === 'banners') {
        const { createBanner } = await import('../actions/banner');
        await createBanner(newItem);
      } else if (activeTab === 'services') {
        const { createService } = await import('../actions/service');
        await createService(newItem);
      } else if (activeTab === 'aboutImage') {
        const { createAboutImage } = await import('../actions/aboutImage');
        await createAboutImage(newItem);
      }
      setNewItem({});
      fetchData(activeTab);
      alert('Item created successfully!');
    } catch (error) {
      alert('Failed to create item');
    } finally {
      setIsSubmitting(false);
    }
  };

  if (!isAuthenticated) {
    return (
      <div className="flex min-h-screen items-center justify-center bg-gray-100">
        <Card className="w-full max-w-sm">
          <CardHeader>
            <CardTitle className="text-center">Admin Login</CardTitle>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleLogin} className="space-y-4">
              <Input 
                type="password" 
                placeholder="Enter Password" 
                value={password} 
                onChange={(e) => setPassword(e.target.value)} 
              />
              {error && <p className="text-red-500 text-sm">{error}</p>}
              <Button type="submit" className="w-full">Login</Button>
            </form>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col">
       <header className="bg-white border-b px-6 py-4 flex justify-between items-center">
         <h1 className="text-xl font-bold">Admin Dashboard</h1>
         <Button variant="outline" onClick={() => setIsAuthenticated(false)}>Logout</Button>
       </header>
       
       <div className="flex-1 container mx-auto p-6">
          <div className="flex flex-wrap gap-2 mb-6">
            {['appointments', 'volunteers', 'contacts', 'banners', 'services', 'aboutImage'].map((tab) => (
              <Button 
                key={tab}
                variant={activeTab === tab ? 'default' : 'outline'}
                onClick={() => setActiveTab(tab as any)}
                className="capitalize"
              >
                {tab}
              </Button>
            ))}
          </div>

          <Card>
            <CardHeader>
              <CardTitle className="capitalize">{activeTab} Management</CardTitle>
            </CardHeader>
            <CardContent>
              
              {/* Add New Form for Banners/Services/About Image */}
              {(activeTab === 'banners' || activeTab === 'services' || activeTab === 'aboutImage') && (
                <div className="mb-8 p-4 bg-muted rounded-lg border">
                  <h3 className="font-bold mb-4">
                    Add New {activeTab === 'banners' ? 'Banner' : activeTab === 'services' ? 'Service' : 'About Page Image'}
                  </h3>
                  <form onSubmit={handleCreate} className="space-y-4">
                    <div className="grid md:grid-cols-2 gap-4">
                      {activeTab !== 'aboutImage' && (
                        <>
                          <Input
                            placeholder="Title"
                            value={newItem.title || ''}
                            onChange={e => setNewItem({...newItem, title: e.target.value})}
                            required
                          />
                          <Input
                            placeholder="Description"
                            value={newItem.description || ''}
                            onChange={e => setNewItem({...newItem, description: e.target.value})}
                            required
                          />
                        </>
                      )}
                      {activeTab === 'banners' && (
                        <Input
                          placeholder="Image URL (e.g., https://...)"
                          value={newItem.imageUrl || ''}
                          onChange={e => setNewItem({...newItem, imageUrl: e.target.value})}
                          required
                        />
                      )}
                      {activeTab === 'aboutImage' && (
                        <>
                          <Input
                            placeholder="Image URL (e.g., https://shanthibhavan.in/images/...)"
                            value={newItem.imageUrl || ''}
                            onChange={e => setNewItem({...newItem, imageUrl: e.target.value})}
                            required
                            className="md:col-span-2"
                          />
                          <Input
                            placeholder="Alt Text (e.g., Shanthibhavan Hospital)"
                            value={newItem.altText || ''}
                            onChange={e => setNewItem({...newItem, altText: e.target.value})}
                            className="md:col-span-2"
                          />
                        </>
                      )}
                      {activeTab === 'services' && (
                        <Input
                          placeholder="Icon Name (Lucide React, e.g., 'Heart')"
                          value={newItem.icon || ''}
                          onChange={e => setNewItem({...newItem, icon: e.target.value})}
                          required
                        />
                      )}
                      {activeTab !== 'aboutImage' && (
                        <Input
                          type="number"
                          placeholder="Order (e.g., 1)"
                          value={newItem.order || ''}
                          onChange={e => setNewItem({...newItem, order: Number(e.target.value)})}
                        />
                      )}
                    </div>
                    <Button type="submit" disabled={isSubmitting}>
                      {isSubmitting ? 'Saving...' : 'Add Item'}
                    </Button>
                  </form>
                </div>
              )}

              {loading ? (
                <div className="text-center py-8">Loading...</div>
              ) : data.length === 0 ? (
                <div className="text-center py-12 text-muted-foreground">
                  <p>No {activeTab} found.</p>
                </div>
              ) : (
                <div className="overflow-x-auto">
                   <table className="w-full text-sm text-left">
                      <thead className="bg-muted text-muted-foreground uppercase text-xs">
                        <tr>
                          {activeTab === 'banners' && <th className="px-6 py-3">Image</th>}
                          <th className="px-6 py-3">Title</th>
                          {activeTab === 'services' && <th className="px-6 py-3">Icon</th>}
                          {(activeTab === 'banners' || activeTab === 'services') && <th className="px-6 py-3">Actions</th>}
                          
                          {/* Common columns for other tabs */}
                          {!['banners', 'services'].includes(activeTab) && (
                            <>
                              <th className="px-6 py-3">Date</th>
                              <th className="px-6 py-3">Name</th>
                              <th className="px-6 py-3">Details</th>
                            </>
                          )}
                        </tr>
                      </thead>
                      <tbody>
                        {data.map((item: any) => (
                          <tr key={item._id} className="border-b bg-white hover:bg-gray-50">
                            
                            {/* Banner Specific */}
                            {activeTab === 'banners' && (
                              <td className="px-6 py-4">
                                <img src={item.imageUrl} alt={item.title} className="w-16 h-10 object-cover rounded" />
                              </td>
                            )}

                            {/* Service Specific */}
                            {activeTab === 'services' && (
                               <td className="px-6 py-4">{item.icon}</td>
                            )}

                            {/* Common Title/Name */}
                            <td className="px-6 py-4 font-medium">{item.title || item.name}</td>

                            {/* Actions for CMS content */}
                            {(activeTab === 'banners' || activeTab === 'services') ? (
                              <td className="px-6 py-4">
                                 <Button variant="outline" size="sm" onClick={() => handleDelete(item._id, activeTab)} className="text-red-500 border-red-500 hover:bg-red-50">Delete</Button>
                              </td>
                            ) : (
                              /* Standard Data View */
                              <>
                                <td className="px-6 py-4">{new Date(item.createdAt).toLocaleDateString()}</td>
                                <td className="px-6 py-4 max-w-xs truncate" title={item.message || item.details || "-"}>
                                   {item.message || item.typeOfService || item.role || "-"}
                                </td>
                              </>
                            )}

                          </tr>
                        ))}
                      </tbody>
                   </table>
                </div>
              )}
            </CardContent>
          </Card>
       </div>
    </div>
  );
}
